#app {
  height: 100%;
  /* overflow: scroll; */
}

.logo {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 200px;
  z-index: 99;
}

.page-block {
  width: 100%;

}

.top-block {
  position: relative;
  background-image: url("../img/top_bg.jpg");
  background-size: cover;
  background-position-x: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.top-block::before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-color: rgba(0, 0, 0, 0.5);
}

.mid-block {
  background: #ffffff;
}

.white-text {
  position: relative;
  color: #ffffff;
  text-align: center;
}

.block-title {
  font-family: "Sacramento", Arial, serif;
  font-size: 100px;
}

.block-text {
  font-family: "Work Sans", Arial, sans-serif;
  font-size: 20px;
  font-weight: 400;
}

.digital-ball {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: rgba(241, 78, 149, 0.8);
  animation: pulse 1s ease infinite;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 4px;
}

.digital-ball > div {
  font-family: "Work Sans", Arial, sans-serif;
  color: #ffffff;
}

.digital-ball > div:nth-child(1) {
  font-size: 30px;
}

.digital-ball > div:nth-child(2) {
  font-size: 12px;
}

.lefttime-display {
  display: flex;
  margin: 25px 0;
}

@media screen and (max-width: 768px) {
  .block-title {
    font-size: 40px;
  }

  .digital-ball {
    width: 80px;
    height: 80px;
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
